@font-face {
    font-family: 'Magnat Head';
    src: url('/assets/fonts/subset-Magnat-HeadMedium.woff2') format('woff2'),
        url('/assets/fonts/subset-Magnat-HeadMedium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Gaultier';
    src: url('/assets/fonts/subset-Gaultier-Regular.woff2') format('woff2'),
        url('/assets/fonts/subset-Gaultier-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Gaultier';
    src: url('/assets/fonts/subset-Gaultier-Bold.woff2') format('woff2'),
        url('/assets/fonts/subset-Gaultier-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'Gaultier';
    src: url('/assets/fonts/Gaultier-RegularItalic.woff2') format('woff2'),
        url('/assets/fonts/Gaultier-RegularItalic.woff') format('woff');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}

:root {
    --color-3: #145d55;
    font-size: 24px;
}

html {
    font-family: 'Gaultier', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', 'SongTi TC', sans-serif;

    background: #fff;
    color: #424242;
}

header {
    margin: 2rem auto;
}

header, main {
    max-width: 40rem;
}

main {
    padding: 0;
    margin: 0 auto;
}

header, h1, h2, h3 {
    text-align: left;
}

header p {
    font-style: italic;
}

h1 {
    font-family: 'Magnat Head', 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', 'SongTi TC', sans-serif;
    font-size: 2em;
    font-weight: 500;
    text-transform: none;
    letter-spacing: 0.02em;
}

main p {
    max-width: 40em;
}

p + img {
    margin-top: 2em;
}

img {
    margin-left: auto;
    margin-right: auto;
}

header img {
    max-width: 40rem;
    margin-bottom: 2rem;
}

#intro {
    display: flex;
    gap: 2rem;
    section {
        padding: 0;
    }
    section + section {
        margin-top: 0;
    }
}

#blurb {
    width: 50%;
}

#info {
    font-style: italic;
    p + p {
        margin-top: 0;
    }
}

#process {
    padding: 2rem 4rem;
    background: no-repeat url('/assets/images/kajo/waves-background.jpg');
    background-color: #477577;
    color: #fff;

    p {
        max-width: 50em;
    }
}

#logo-o {
    shape-outside: circle();
    width: 12em;
    margin: 2em;
    display: inline;
    float: right;
}

#palette {
    img + img {
        margin-top: 2rem;
    }
}

#font-palette {
    max-width: 30rem;
}

#color-palette {
    max-width: 15rem;
}

#outcomes {
    display: flex;
    gap: 2rem;

    div {
        margin-top: 5rem;
    }

    figure {
        position: relative;
    }

    figcaption {
        background-color: #FCC262;
        padding: 1em 1.5em;
        font-weight: normal;

        position: absolute;
        top: 2em;
        right: -10em;
    }
}

footer {
    margin-top: 5em;
}